
import { useStore } from '@/store';
import { MutationType } from '@/store/mutations';
import { defineComponent, onMounted, reactive, toRefs } from 'vue';

export default defineComponent({
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  setup(props) {
    const store = useStore();
    const state = reactive({
      title: '',
      description: '',
      createdBy: '',
      assignedTo: '',
    });

    const setFields = () => {
      const task = store.getters.getTaskById(Number(props.id));
      if (task) {
        const { title, description, createdBy, assignedTo } = task;
        state.title = title;
        state.description = description;
        state.createdBy = createdBy;
        state.assignedTo = assignedTo;
      }
    };

    const closeModal = () => {
      store.commit(MutationType.SetEditModal, {
        showModal: false,
        taskId: undefined,
      });
    };

    onMounted(() => setFields());

    const updateTask = () => {
      if (
        !state.title ||
        !state.description ||
        !state.createdBy ||
        !state.assignedTo
      )
        return;

      const task = {
        id: props.id,
        title: state.title,
        description: state.description,
        createdBy: state.createdBy,
        assignedTo: state.assignedTo,
        completed: false,
        editing: false,
      };

      store.commit(MutationType.UpdateTask, task);

      state.title = '';
      state.createdBy = '';
      state.assignedTo = '';
      state.description = '';

      closeModal();
    };

    return {
      ...toRefs(state),
      closeModal,
      updateTask,
    };
  },
});
